import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the CharacterProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CharacterProvider {

  constructor(public http: HttpClient) {
    console.log('Hello CharacterProvider Provider');
  }

  
  check = Math.floor((Math.random() * 31) + 1);

  c = [
  	"HOWDY",
  	"Hero",
  	"Mentor",
  	"Ally",
  	"Comic Relief",
  	"Herald",
  	"Trickster",
  	"Shapeshifter",
  	"Guardian",
  	"Shadow",
  	"Absent-Minded Professor",
  	"Pervert",
  	"Stupid Doe Eyed Believer",
  	"Cold Hearted Skeptic",
  	"Bumbling Dad",
  	"Charmer",
  	"Unwilling Friend",
  	"Caretaker",
  	"Asshole With A Heart of Gold",
  	"Prodigy",
  	"Wet Blanket",
  	"Oblivious Nerd",
  	"Creepy Gross Anime Fan",
  	"Anti-Hero",
  	"Fat One",
  	"Secret Sibling",
  	"Amazon",
  	"Flamboyant One",
  	"Pink Girly Girl",
  	"Mysterious Stranger",
  	"Ambiguously Gendered",
  	"Geezer"
  ];

  cr = this.c[this.check];

}



