import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the PlaceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PlaceProvider {

  constructor(public http: HttpClient) {
    console.log('Hello PlaceProvider Provider');
  }


  check = Math.floor((Math.random() * 24) + 1);

  p = [
  	"HOWDY!",
  	"on the street",
  	"at home",
  	"at a crime scene",
  	"in a secret hideout",
  	"on the beach",
  	"on the mountain side",
  	"on an open grassland",
  	"on a glacier",
  	"in a desert",
  	"in the city",
  	"in an haunted location",
  	"in a mansion",
  	"at the casino",
  	"at the train station",
  	"at the power plant",
  	"in a castle",
  	"lost in a jungle",
  	"in a witch's cave",
  	"in a video game bonus stage",
  	"riding on top of a plane",
  	"in an elevator",
  	"in a land of candy",
  	"on another planet",
  	"at a funeral"
  ];

  pr = this.p[this.check];
}



